import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-design-circuit',
  templateUrl: './design-circuit.component.html',
  styleUrls: ['./design-circuit.component.scss']
})
export class DesignCircuitComponent implements OnInit {
  dangerousVideoUrl: string;
  videoUrl: any;

  constructor(private sanitizer: DomSanitizer) {
  }

  updateVideoUrl(url:string) {
    // Appending an ID to a YouTube URL is safe.
    // Always make sure to construct SafeValue objects as
    // close as possible to the input data so
    // that it's easier to check if the value is safe.
    this.dangerousVideoUrl = url;
    // this.dangerousVideoUrl = 'https://www.squareink.xyz';
    this.videoUrl = this.sanitizer.bypassSecurityTrustResourceUrl(this.dangerousVideoUrl);
  }

  ngOnInit() {
    this.updateVideoUrl('../../assets/design-circuit/index.html');
  }
}