import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-block-project',
  templateUrl: './block-project.component.html',
  styleUrls: ['./block-project.component.scss']
})
export class BlockProjectComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  scrollToElement($scrollId:string): void {
    if($scrollId==null){
      return;
    }
    var element = document.getElementById($scrollId);
    element.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
  }
}
