import { Component } from '@angular/core';
import { AfterViewInit, ViewChild } from '@angular/core';
import { BlockProjectComponent } from './block-project/block-project.component'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  @ViewChild(BlockProjectComponent)
  private projectComponent: BlockProjectComponent;

  title = 'portfolio-website';

  onSelected(scrollId:string){
    this.projectComponent.scrollToElement(scrollId);
  }
}
